.mySwiper {
    width: 100%;
    height: 226px;
    border-radius: 10px;
}

.swiper_slide {
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper_slide img {
    display: block;
    width: 100%;
    height: 226px;
    object-fit: cover;
    border-radius: 10px;
}